export interface ValuePriority {
  value: string;
  priority: number;
}

export abstract class Tag {
  private cache: Map<string, Map<string, ValuePriority>> = new Map();
  abstract readonly tag: string;
  readonly deps: string[] = [];
  abstract build(input?: string, ...args: string[]): Map<string, ValuePriority>;
  protected setCache(input: string, value: Map<string, ValuePriority>) {
    this.cache.set(input, value);
  }
  protected getCache(input: string) {
    return this.cache.get(input);
  }
}
