import {inject, InjectionToken} from '@angular/core';

import {Tag} from './tag';
import {FlexAlign} from './flex/flex-align';
import {FlexFill} from './flex/flex-fill';
import {FlexOrder} from './flex/flex-order';
import {FlexOffset} from './flex/flex-offset';
import {LayoutAlign} from './flex/layout-align';


export const FLEX_TAGS = [FlexAlign, FlexFill, FlexOrder, FlexOffset, LayoutAlign];
export const GRID_TAGS = [];

export const DEFAULT_TAGS = [...FLEX_TAGS, ...GRID_TAGS];
export const LAYOUT_TAGS = new InjectionToken<Array<Array<Tag>>>('Angular Layout Tags');

export const FLEX_PROVIDER = {
  provide: LAYOUT_TAGS,
  useValue: FLEX_TAGS,
  multi: true,
};

export const GRID_PROVIDER = {
  provide: LAYOUT_TAGS,
  useValue: GRID_TAGS,
  multi: true,
};

export const TAGS_PROVIDER = {
  provide: LAYOUT_TAGS,
  useValue: DEFAULT_TAGS,
  multi: true,
};

export const TAGS = new InjectionToken<Map<string, Tag>>('Angular Layout Condensed Tags', {
  providedIn: 'root',
  factory: () => {
    const providedTags = inject(LAYOUT_TAGS);
    const tagsMap: Map<string, Tag> = new Map();

    providedTags.forEach(tags => {
      tags.forEach(tag => {
        tagsMap.set(tag.tag, tag.constructor());
      });
    });

    return tagsMap;
  }
});
